ngOnInit() {

  this.user = {};
  this.user.isEmailNotify = true;

  //get apartment blocks
  // this.apartmentService.getApartmentBlockByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res: any) => {

  //   this.blocksData = res;

  // });
  // this.userService.getAllRoles(parseInt(this.cookieService.get('apartmentId'))).subscribe((res: any) => {
  //   console.log(res);
  //   this.userRolesData = res;

  // });

  let params = {
    apartmentId: parseInt(this.cookieService.get('apartmentId'))
  }

  //get apartment blocks
  this.apartmentService.getApartmentBlockByApartmentId(params).subscribe((res:any) => {
    this.blocksData = res;
  });

  let roleParams = {
    ApartmentId: parseInt(this.cookieService.get('apartmentId'))
  }

  this.userService.getAllRoles(roleParams).subscribe((res:any) => {
    this.userRolesData = res;
  });


  if (this.route.params['value'].id != undefined) {

    this.isEditUser = true;

    let useridParam = {
      userid : this.route.params['value'].id
    }
    this.userService.getUserById(useridParam).subscribe((res: any) => {

      this.user = res[0];
      this.genderType = this.user.genderId.toString();



      // get role

      let userIdParam = {
        userId : this.route.params['value'].id
      }

      this.userService.getRolesByUserId(userIdParam).subscribe((data: any) => {

        this.userType = data[0].roleId.toString();

      },
        error => {

        });

      //get block
      this.apartmentService.getApartmentBlockByUserId(userIdParam).subscribe((block: any) => {


        this.apartmentBlockId = block[0].apartmentBlockId.toString();
        this.getUnits();

      },
        error => {

        });

    },
      error => {

      });

  }

}


