import { Component, Injector, OnInit, ViewChild, HostListener } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { MatDialog } from '@angular/material/dialog';

import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';

import { UserService } from 'src/app/api/controllers/User';
import { SharedService } from '../../../../shared/services/shared.service';
import { ConstantsService } from '../../../../shared/services/constants.service';
import { CookieService } from 'ngx-cookie-service';
import { ModalService } from 'src/app/shared/services/modal.service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-signuprequest',
  templateUrl: './signuprequest.component.html',
  styleUrls: ['./signuprequest.component.scss']
})
export class SignuprequestComponent implements OnInit {
  
  unitListData: any;
  unitData: string = "";

  totalItems: any;
  modalService: any;
  viewUserId:any;

  isUserDataLoaded: boolean = false;
  isProfile: boolean = false;
  isMobile: boolean = false;
  isUserSelected: boolean = false;

  columnData:any ;
  gridSourceData:any;
  
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
 
  constructor(
    private injector: Injector,
    public dialog: MatDialog,
    private router: Router,
    private userService: UserService,
    private constantsService: ConstantsService,
    private sharedService: SharedService,
    private cookieService: CookieService) {
    this.modalService = this.injector.get(ModalService);
  }
  
  getBookedDate(date) {
    return moment(date).format("DD/MM/YYYY");
  }
  
  onGlSearchFilter() {

    if(this.unitData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.unitData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }

  getPrintParams(event){
    this.datagrid.exportdata(event, 'selfSignupUser');
  }

  viewUserInfo(id) {

    this.isProfile = false;

    setTimeout(() => {

      this.isProfile = true;

    }, 10);

    setTimeout(() => {

      var elem = document.querySelector('.user-info-card');
      var scrollTo = elem.getBoundingClientRect().top - 100;

      window.scroll({
        top: scrollTo,
        behavior: 'smooth'
      });

    }, 100);


    this.viewUserId = id;

    this.router.navigate(['ams/unit&users/unapproved/basic', this.viewUserId]);



  }
  showConfirmModal(data) {
    this.modalService.showConfirmModal(data.signupUserRequestId);
    var params = {
      signupUserId: data.signupUserRequestId,
      apartmentId: data.apartmentId
    }
    this.userService.updateSignupUserRequestByApartmentId(params).subscribe((res: any) => {
      if (res.message) {
        this.sharedService.setAlertMessage("Signup deleted successfully.");
        this.router.navigate(['ams/unit&users/usersignuprequest', this.viewUserId]);
      }
    },
      error => {
        console.log(error);
      }
    );
  }

  @HostListener('window:onEditUser', ['$event.detail'])
  onEditUser(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    dataRecord.mode = true;
    this.modalService.showSignUpdetailsModal(dataRecord);
  }

  @HostListener('window:onUserDelete', ['$event.detail'])
  onUserDelete(detail) {
    this.modalService.showConfirmModal(detail.rowId);
  }
  
  @HostListener('window:onViewUser', ['$event.detail'])
  onViewUser(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    dataRecord.mode = false;
    this.modalService.showSignUpdetailsModal(dataRecord);
  }
  
  EditUserInfo(data) {
    data.mode = true;
    this.modalService.showSignUpdetailsModal(data);
  }
  viewSignup(data) {
    data.mode = false;
    this.modalService.showSignUpdetailsModal(data);
  }

  ngOnInit() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.userService.getAllSignupUserRequestByApartmentId(params).subscribe((res: any) => {

      var unitListData = res;

      this.gridSourceData = {
        localdata: unitListData,
        datatype: "array"
      }

      this.unitListData = new jqx.dataAdapter(this.gridSourceData);
      this.totalItems = unitListData.length;

      this.columnData = [{
        text: 'Request ID',
        datafield: 'signupUserRequestId',
        width: 100,
        pinned: true,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'Name',
        datafield: 'firstName',
        minwidth: 150,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'Phone Number',
        datafield: 'phoneNumber',
        minwidth: 120,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'Email',
        datafield: 'emailId',
        minwidth: 150,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
          text: 'Tower Unit',
          datafield: 'block_Unit',
          minwidth: 120,
          cellsrenderer: cellsrenderer,
          renderer: columnrenderer
      },
      {
        text: 'Requested On',
        datafield: 'insertedOn',
        minwidth: 120,
        cellsrenderer: (row, column, value): string => {
          return '<div class="jqx-custom-inner-cell">' + moment(value).format(this.constantsService.dateFormat) + '</div>'
        },
        renderer: columnrenderer
      },
      {
        text: 'Actions',
        cellsalign: 'center', 
        align: 'center',
        width: 120,
        cellsrenderer: (row: number): string => {
          return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="viewUserEvent('+ row +')"><i class="fa fa-eye icon view" aria-hidden="true"></i></a><a href="javascript:void(0)" class="mr-2" onClick="editUserEvent('+ row +')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a><a href="javascript:void(0)" class="mr-2" onClick="showConfirmDeleteEvent('+ row +')"><i class="fa fa-trash icon delete" aria-hidden="true"></i></a></div>'
        },
        renderer: columnrenderer
      }
      ]

      this.isUserDataLoaded = true;

    },
      error => {
        console.log(error);
    });

    // delete item
    this.sharedService.unitlistdeleteindexcast.subscribe( id => {

      if(id != null){

        let dataRecord = this.datagrid.getrowdata(id)
        let signupUserId = dataRecord.signupUserRequestId

        let params = {
          signupUserId: signupUserId,
          apartmentId: parseInt(this.cookieService.get('apartmentId'))
        }


        this.userService.updateSignupUserRequestByApartmentId(params).subscribe((res:any) => {

          setTimeout(() => {

            this.datagrid.deleterow(id);
            this.totalItems = this.unitListData.length;

            this.sharedService.setAlertMessage("Signup deleted successfully.");
            this.sharedService.setUnitListDeleteIndex(null);

          }, 500)

          

        }, error => {
          console.log(error);
        });

        
      }

    });


  }

}

function editUserEvent(row){
  var event = new CustomEvent('onEditUser', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editUserEvent = editUserEvent;

function viewUserEvent(row){
  var event = new CustomEvent('onViewUser', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).viewUserEvent = viewUserEvent;


function showConfirmDeleteEvent(row){
  var event = new CustomEvent('onUserDelete', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).showConfirmDeleteEvent = showConfirmDeleteEvent;
